<template>
  <div id="m_reg-obituary">
    <MobileHeader
      title="추모영상"
      class="mb-0"
      right-icon="mdi-help-circle-outline"
      @close="routingBack"
      @clickRightIcon="move('/')"
    />

    <div
      class="info-box px-5"
      style="
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
      "
    >
      <div>
        <p style="font-size: 14px">
          선택한 상주의
          <font>휴대폰 번호</font>
          로 부고가 발송됩니다
        </p>
        <div class="leader-info" style="justify-content: center">
          <span> <font>추모영상 링크</font>가 전송됩니다</span>
        </div>
      </div>
    </div>
    <main>
      <section
        v-for="(memberType, id) in obituaryMemberList"
        :key="id"
        class="title-inputs__wrap phone-box"
      >
        <div class="title-box">
          <h4 style="font-size: 14px">{{ memberType.type }}</h4>
        </div>

        <div
          v-for="(member, index) in memberType.members"
          :key="index"
          class="member-chip__wraps pb-2"
          style="justify-content: space-between; align-items: center"
        >
          <div
            style="
              display: flex;
              justify-content: space-between;
              align-items: center;
              text-align: start;
            "
          >
            <v-chip class="member-chip">{{ member.obituaryMemberName }}</v-chip>

            <div style="width: 240px">
              <v-text-field
                v-model="member.obituaryMemberPhone"
                type="text"
                colr="main"
                hide-details
                placeholder="휴대폰 번호 입력"
                maxlength="11"
                class="pt-0 mt-0"
              />
            </div>
          </div>

          <div
            class="check-box"
            :class="{
              checked: member.obituaryMemberLeader
            }"
            @click="changeLeader(id, index)"
          ></div>
        </div>
      </section>

      <div
        style="
          width: 100%;
          text-align: center;
          padding: 0px 16px;
          margin-top: 80px;
        "
      >
        <button
          style="
            width: 100%;
            height: 48px;

            font-family: var(--noto3);
            font-weight: 700;
            font-size: 18px;
            color: #fff;

            margin: auto;

            background: var(--main-color);
            border-radius: 6px;
          "
          @click="requestObituaryOnlyMovie"
        >
          전송하기
        </button>
      </div>
    </main>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  components: { MobileHeader },

  data() {
    return {
      id: this.$route.query.id ? this.$route.query.id : "",
      obituaryMemberList: []
    };
  },

  computed: {
    ...mapGetters(["getObituaryDetail", "memberInfo", "getloadingState"])
  },

  mounted() {
    this.$store.dispatch("REQUEST_OBITUARY");
    const parameter = {
      id: this.id
    };
    this.$store.dispatch("REQUEST_OBITUARY_DETAIL", parameter).then(data => {
      if (data) {
        let obituaryDetailCopy = JSON.parse(
          JSON.stringify(this.getObituaryDetail)
        );

        if (
          obituaryDetailCopy.obituaryObituaryMember !== null &&
          obituaryDetailCopy.obituaryObituaryMember !== undefined
        ) {
          obituaryDetailCopy.obituaryObituaryMember.map(v => {
            const findIndex = this.obituaryMemberList.findIndex(
              arg => arg.type === v.obituaryMemberType
            );
            if (findIndex > -1) {
              this.obituaryMemberList[findIndex].members.push(v);
            } else {
              this.obituaryMemberList.push({
                type: v.obituaryMemberType,
                members: [v]
              });
            }

            return false;
          });
        }
      }
    });
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },

  methods: {
    routingBack() {
      this.$router.go(-1);
    },
    move(path) {
      this.$router.push(path);
    },

    changeLeader(id, memberIdx) {
      const member = this.obituaryMemberList[id].members[memberIdx];

      const preLeader = this.obituaryMemberList
        .map(v => v.members)
        .flat()
        .filter(args => args.obituaryMemberLeader === true);

      // eslint-disable-next-line
      preLeader.map(v => {
        v.obituaryMemberLeader = false;
        return null;
      });
      member.obituaryMemberLeader = true;
    },

    requestObituaryOnlyMovie() {
      const preLeader = this.obituaryMemberList
        .map(v => v.members)
        .flat()
        .filter(args => args.obituaryMemberLeader === true)[0];

      if (!preLeader) {
        return alert("선택된 상주가 없습니다");
      }
      if (preLeader.obituaryMemberPhone === "") {
        return alert("선택한 상주의 번호를 입력하여 주십시오");
      }
      if (
        confirm(
          `${preLeader.obituaryMemberName} 님에게 제작 안내 메시지가 발송됩니다. 전송하시겠습니까? (제3자 개인정보 동의로 간주함)`
        )
      ) {
        const id = preLeader.id;

        const parameter = {
          id
        };
        this.$store.dispatch("REQUEST_OBITUARY_REPLY_MOVIE", parameter);
      }
    }
  }
};
</script>

